<template>
	<div id="activity-svipRedpacket">
		<public-header :title="page_title"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title" class="">开始时间</div>
					<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('start')">
						{{this.datePicker.start ? this.datePicker.start: '点击选择'}}
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title" class="">结束时间</div>
					<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('end')">
						{{this.datePicker.end ? this.datePicker.end: '点击选择'}}
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="红包金额">
					<div class="input-unit" slot="right-icon">
						<input type="number" v-model="activity.discount">
						<span>元</span>
					</div>
				</van-cell>
				<van-cell title="领取后几天内有效">
					<div class="input-unit" slot="right-icon">
						<input type="number" v-model="activity.use_days_limit">
						<span>天</span>
					</div>
				</van-cell>
				<van-cell title="每日发放红包数量">
					<div class="input-unit" slot="right-icon">
						<input type="number" v-model="activity.amount">
						<span>个</span>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" :disabled="!islegal" block class="font-16 bg-info" @click="onSubmit()">确认并保存</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--时间弹出层-->
		<van-popup class="popup-time" v-model="datePicker.status" position="bottom">
			<van-datetime-picker
					v-model="datePicker.currentDate"
					type="datetime"
					:min-data="datePicker.currentDate"
					:max-date="datePicker.maxDate"
					@confirm="onConfirmTime"
					@cancel="onToggleDatePicker"
			/>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				page_title: '',
				datePicker: {
					type: '',
					maxDate: new Date(2050, 12),
					currentDate: new Date(),
					status: false,
					start: '',
					end: '',
				},
				filter: {
					items: {
						type: ''
					}
				},
				activity: {
					discount: 0,
					use_days_limit: 0,
					amount: 0
				},
				showPreLoading: true,
				islegal: false,
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/activity/index/activity_svipRedpacket',
					data: {
						filter: this.filter.items
					},
					success: function(res) {
						that.islegal = true;
						that.page_title = res.page_title;
						let nowtime = that.util.formatDate(that.datePicker.currentDate);
						that.datePicker.start = that.datePicker.end = nowtime;
					}
				})
			},
			onToggleDatePicker(type) {
				this.datePicker.status = !this.datePicker.status;
				if(type) {
					this.datePicker.type = type;
				}
			},
			onConfirmTime(value) {
				let time = this.util.formatDate(value);
				this.datePicker[this.datePicker.type] = time;
				this.onToggleDatePicker();
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				if(!that.activity.discount) {
					that.util.$toast('红包金额不能为空');
					return false;
				}
				if(that.activity.use_days_limit < 0) {
					that.util.$toast('红包有效期必须大于或等于零');
					return false;
				}
				if(that.activity.amount <= 0) {
					that.util.$toast('每日限领红包数量必须大于零');
					return false;
				}
				var params = {
					starttime: that.datePicker.start,
					endtime: that.datePicker.end,
					discount: that.activity.discount,
					use_days_limit: that.activity.use_days_limit,
					amount: that.activity.amount,
				};
				setData({
					vue: that,
					url: 'manage/activity/index/activity_svipRedpacket',
					data: {
						type: that.filter.items.type,
						params: params
					},
					redirect: that.util.getUrl({path: 'pages/activity/index'}),
				});
			}
		},
		mounted() {
			this.onLoad();
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.type) {
				this.filter.items.type = this.query.type;
			}
		}
	}
</script>

<style>

</style>


